.wallet{
  border-bottom: 1px solid rgba(0,0,0,0.1); //TODO: eval true color
  padding-bottom: 15px;
  margin-bottom: 25px;
  &:after{
    content: '';
    clear: both;
    display: block;
  }
  &__info{
    width: ~"calc(100% - 108px)";
    margin-bottom: 20px;
  }
  &__label, &__edit, &__balance{
    display: inline-block;
    vertical-align: top;
    color: @blue-dark;
  }
  &__label{
    margin-right: 17px;
  }
  &__edit{
    //cursor: pointer;
  }
  &__balance{
    float: right;
  }
  .field-with-icon{
    width: ~"calc(100% - 108px)";
    margin-right: 18px;
    float: left;
  }
  .btn{
    float: left;
    margin-top: 7px;
  }
}
.wallet--send{
  .field-with-icon{
    width: 100%;
    margin-right: 0px;
    float: none;
  }
  .wallet__info{
    width: 100%;
  }
}
.wallet__qr{
  text-align: center;
  padding: 20px 0;
}
#qrimage{
  display: none;
  margin: auto;
  &.visible{
    display: block;
  }
}
#toggle-qr{
  cursor: pointer;
}