.tooltip{
  width: 392px;
  padding: 3px;
  position: relative;
  background: #FFFFFF;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  position: absolute;
  //display: none;
  .tooltip__title{
    color: @blue-dark;
    font-size: 18em/16;
    border-bottom: 1px solid @gray;
    padding: 20px 0;
    padding-left: 21px;
  }
  .tooltip__content{
    padding: 12px 20px;
  }
  .tooltip__close{
    position: absolute;
    right: 7px;
    top: 7px;
    cursor: pointer;
    img{
      vertical-align: top;
    }
  }
  &.visible{
    display: block;
  }
}