.btn{
  color: @white;
  border-radius: 4px;
  text-transform: uppercase;
  width: 260px;
  padding: 12px 0;
  text-align: center;
  cursor: pointer;
  //margin: auto;
  //margin-bottom: 28px;
  display: inline-block;
  &--disabled{
    background-color: gray!important;
  }
  &--green{
    background-color: @green;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.03);
  }
  &--blue{
    background: linear-gradient(131.95deg, #041144 -62.91%, #339BE6 100%);
    box-shadow: 0px 2px 4px rgba(12, 34, 103, 0.2);
  }
  &--blue-dark{
    border: 0;
    color: @blue-dark;
  }
  &--transparent{
    background: rgba(255, 255, 255, 0.1);
  }
  &--white{
    color: @blue;
    background: #FFFFFF;
    border: 1px solid rgba(51, 155, 230, 0.1);
  }
  &--small{
    width: 80px;
  }
  &--thin{
    width: 80px;
    padding: 5px 0;
    font-size: 14px;
    &.btn--left img{
      left: 5px;
      top: 10px;
    }
  }
  &--medium{
    width: 160px;
  }
  &--long{
    width: 100%;
  }
  &--extra-small{
    height: 36px;
    width: 90px;
    padding: 6px 0;
  }
  &--left{
    position: relative;
    img{
      position: absolute;
      left: 20px;
      top: 15px;
    }
  }
  &--right{
    position: relative;
    img{
      position: absolute;
      right: 20px;
      top: 15px;
    }
  }
}