.passphrase{
  margin: 40px 0 0px;
  &:after{
    content: '';
    display: block;
    clear: both;
  }
  &__item{
    width: 100%/3;
    float: left;
    text-align: center;
    padding: 0 10px;
    margin-bottom: 30px;
  }
  &__copy{
    margin-bottom: 16px;
    font-size: 18em/16;
    text-align: center;
    cursor: pointer;
    &:hover{
      text-decoration: underline;
    }
  }
}
.copy-btn{
  font-size: 20px;
  margin-bottom: 30px;
  display: block;
}