.tabs{
  &__labels{
    height: 56px;
    background: #FFFFFF;
    box-shadow: 0px 6px 8px rgba(4, 17, 68, 0.1);
    border-radius: 4px;
    position: relative;
    top: -56px/3*2;
    &:after{
      content: '';
      clear: both;
      display: block;
    }
  }
  &__label{
    float: left;
    width: 25%;
    display: inline-block;
    border-right: 1px solid #E0E0E0;
    padding: 18px 0;
    //padding-left: 45px;
    position: relative;
    text-align: center;
    cursor: pointer;
    &:last-child{
      border: 0;
    }
    &.active{
      color: @blue-dark;
    }
    .radio{
      position: absolute;
      top: 24px;
      left: 20px;
    }
  }
  &__content{
    padding: 44px 0;
    display: none;
    &.active{
      display: block;
    }
  }
}