.main-banner{
  height: 100vh;
  min-height: 600px;
  max-height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background: linear-gradient(111.02deg, #041144 6.95%, #339BE6 111.83%);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  &__content{
    //margin: auto;
    //width: 60%;
  }
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 160px;
    background-image: url(../img/svg/waves.svg);
    background-size: cover;
    background-repeat: no-repeat;
  }
}