.bottom-bar{

  text-align: center;
  position: absolute;
  bottom: -35px;
  width: 100%;
  img{
    width: 100%;
  }
  &__content{
    background: #FFFFFF;
    box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    height: 70px;
    margin: auto;
    padding-top: 14px;
    padding-left: 50px;
    padding-right: 23px;
    font-size: 14px;
    font-weight: 700;
    color: fade(@blue-dark, 70);
  }
  &__left{
    float: left;
    text-align: left;
  }
  &__center{
    position: absolute;
    margin: auto;
    width: 240px;
    height: 90px;
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    left: 0;
    right: 0;
    top: -10px;
    padding: 21px 30px;
  }
  &__right{
    float: right;
  }
}
.usefull-links{
  &__item{
    display: inline-block;
    vertical-align: top;
    padding: 0 10px;
    &:first-child{
      border-right: 1px solid #E0E0E0;
    }
  }
}
