//COLORS

.white{
  color: @white;
}
.blue{
  color: @blue;
}
.blue-dark{
  color: @blue-dark;
}
.green{
  color: @green;
}
.orange{
  color: @orange;
}
.gray{
  color: @gray;
}
.gray-medium{
  color: @gray-medium;
}

//TEXTS
.note{
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  padding: 5px 0;
}


//CSS PROPS
.light{
  font-weight: 300;
}
.bold{
  font-weight: 700;
}

.tal{
  text-align: left;
}
.tar{
  text-align: right;
}
.underlined{
  text-decoration: underline;
}

.pl0{
  padding-left: 0;
}
.pr0{
  padding-right: 0;
}
.clearfix{
  clear: both;
}
.hidden{
  display: none !important;
}
