.exchange-steps{
  //background: #FAFBFD;
  padding: 30px 0;
  textarea{
    font-weight: 700;
    color: @blue;
    padding: 13px 20px;
    text-align: center;
    resize: none;
    height: auto;
    line-height: 24px;
  }
  .block{
    position: relative;
  }
  .exchange-progress{
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .exchange-status{
    color: @blue-dark;
    margin: 20px 0 10px;
    font-weight: 300;
  }
  .btn{
    margin-top: 12px;
    margin-bottom: 18px;
    width: 180px;
  }
}
.exchange-step{
  margin-bottom: 20px;
}
.step__title{
  margin-bottom: 10px;
}
.exchange-progress{
  background-image: url(../img/svg/load-exchange.svg);
  height: 30px;
  width: 100%;
  background-repeat: repeat-x;
  text-align: center;
  font-size: 14px;
  color: @white;
  padding: 5px 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  animation: displace 2000ms linear infinite;
  //&:hover{
  //  animation: displace 2000ms linear infinite;
  //}
}
@keyframes displace{
  from{
    background-position: 0 0;
  }
  to{
    background-position: 40px 0;
  }
}