.field-with-icon{
  .field();
  position: relative;
  display: inline-block;
  .field-icon{
    position: absolute;
    right: 10px;
    top: 0;
    bottom: 0;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;

  }
  input[type="text"],input[type="password"]{
    padding-right: 45px;
    &.invalid{
      box-shadow: inset 0px 0px 4px 2px rgba(255, 0, 0, 1);
    }
  }
}

.field{
  width: 300px;
  input[type="text"],input[type="password"],textarea{
    width: 100%;
    height: 46px;
    font-size: 14px;
    background: @white;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.03);
    border-radius: 4px;
    border: 0;
    outline: none;
    padding: 0 25px;
    color: gray;
    &.invalid{
      box-shadow: inset 0px 0px 4px 2px rgba(255, 0, 0, 1);
    }
  }
  select{
    appearance: none;
    width: 100%;
    height: 46px;
    font-size: 14px;
    border: 0;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.03);
    padding: 0 25px;
    position: relative;
    &:focus{
      outline: none;
      box-shadow: none;
    }
  }
  select+img{
    position: absolute;
    right: 10px;
    top: 13px;
    pointer-events: none;
  }
  &--big{
    input[type="text"]{
      height: 50px;
    }
  }
  &--thin{
    input[type="text"]{
      height: 27px;
      padding: 0 10px;
    }
  }
  &--fullwidth{
    width: 100%;
  }
  &--bordered{
    input[type="text"], textarea{
      border: 2px solid #F4F4F4;
      box-sizing: border-box;
      border-radius: 4px;
      box-shadow: none;
      padding: 10px;
    }
  }
  &--gray{
    input[type="text"], select, textarea{
      background-color: @gray-light;
      box-shadow: none;
    }
  }
  &--white{
    input[type="text"]{
      border: 1px solid #BDBDBD;
    }
  }
  &--text-right{
    input,select{
      text-align: right;
    }
  }
  &--with-hint{
    position: relative;
    .field__hint{
      position: absolute;
      font-size: 12px;
      color: @blue-dark;
      text-align: right;
      left: 0;
      top: -17px;
      &--right{
        left: auto;
        right: 0;
      }
    }
  }
}

.radio{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid @gray;
  position: relative;
  display: inline-block;
  &>span{
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: transparent;
    position: absolute;
    top: 2px;
    left: 2px;
  }
  .active &{
    //border-color: @blue-dark;
    &>span{
      background-color: @blue-dark;
    }
  }
}

//FORMS
.login-form{
  margin-bottom: 87px;
  &>div{
    display: inline-block;
    vertical-align: top;
  }
  .field-with-icon{
    margin-right: 20px;
  }
  &--registration{
    margin-top: 100px;
  }
}
.contacts-form{
  margin-top: 30px;
  .field{
    margin-bottom: 23px;
  }
  textarea{
    resize: none;
    height: 107px;
  }
  .btn{
    float: right;
  }
}


