.transactions-table{
  &__usd{
    color: @gray;
    font-size: 14px;
  }
  &__xmr{
    font-weight: 700;
  }
  &__time{

  }
  &__details{
    display: none;
    background-color: @blue;
    color: @white;
    width: 100%;
    height: 100%;
  }
  tr:hover{
    .transactions-table__time{
      display: none;
    }
    .transactions-table__details{
      display: block;
    }
    td:last-child{
      background-color: @blue;
    }
  }
}

.load-more{
  background-position: 0 0;
  background-repeat: repeat-x;
  width: 100%;
  height: 50px;
  color: @orange;
  text-align: center;
  font-weight: 700;
  padding: 14px 0;
  cursor: pointer;
  //position: fixed;
  //
  //left: 0;
  //bottom: 100px;
  //width: 100%;
  //animation: displace 2000ms linear infinite;
  &.loading{
    animation: displace 2000ms linear infinite;
    background: url(../img/svg/load-more.svg);
  }
  //&:hover{
  //  animation: displace 2000ms linear infinite;
  //}
}
@keyframes displace{
  from{
    background-position: 0 0;
  }
  to{
    background-position: 120px 0;
  }
}

.import-transactions{
  &__text{
    background-color: rgba(51,155,230,0.1);
    padding: 20px;
    text-align: center;
    color: @blue-dark;
  }
  &__btn{
    margin-top: 27px;
    float: right;
  }
  &:after{
    content: '';
    display: block;
    clear: both;
  }
}
.import-step{
  padding: 0;

  textarea{
    padding: 10px 20px;
    text-align: center;
    color: @blue-dark;
    font-weight: 700;
    height: 90px;
    margin-bottom: 10px;
  }
  .import-step__content{
    position: relative;
    padding: 20px;
    padding-bottom: 40px;
  }
  .exchange-progress{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  img{
    position: relative;
    left: 6px;

  }
}