.table {
  width: 100%;
  //border-collapse: collapse;
  th {
    font-weight: 400;
    padding-bottom: 20px;
    //margin-bottom: 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  }
  td{
    text-align: center;
    padding: 11px 0;
    color: @blue-dark;
  }
  tr{
    &:hover {
      background: rgba(51, 155, 230, 0.1);
      cursor: pointer;
    }
    &:nth-child(2) {
      border-spacing: 10px;
    }
  }
  tr&__head {
    &:hover {
      background: @white;
      cursor: auto;
    }
  }
  &__empty-tr{
    height: 10px;
  }
}
.support-table{
  margin: auto;
  td{
    padding: 8px 0;
    text-align: left;
    font-size: 18em/16;

  }
  td:first-child{
    color: @blue;
    font-size: 1.25em;
    font-weight: 700;
    padding-right: 10px;
  }
}
.transaction-details-table{
  margin: auto;
  td{
    padding: 6px 0;
    //text-align: left;
    font-size: 18em/16;
  }
  td:first-child{
    color: @blue-dark;
    font-size: 18em/16;
  }
  td:last-child{
    padding-left: 10px;
  }
  td&__hash{
    font-size: 0.8em;
  }
}