@import url('https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700');

body{
  font-family: Quicksand;
  font-size: 16px;
  color: @gray;
  position: relative;
  min-width: 1024px;
}

header{
  background: rgba(255, 255, 255, 0.05);
  height: 60px;
  width: 100%;
  padding: 18px 90px 15px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2000;
}
.header-with-controls{
  .header-controls{
    float: right;
    text-transform: uppercase;
    color: @white;
    a{
      display: inline-block;
      margin-left: 30px;
    }
  }
  .logo{
    display: inline-block;
  }
  a{
    color: @white;
  }
}

h1{
  font-size: 2.875em;
  font-weight: 400;
}
h2{
  font-size: 2.25em;
  font-weight: 400;
}
h3{
  font-size: 1.25em;
  font-weight: 400;
}

p{
  line-height: 2em;
  margin: 1em 0;
}

a{
  color: inherit;
  &:hover{
    color: inherit;
  }
}

.footer{
  background-color: @white;
  padding: 30px 50px 26px;
  text-align: center;
  &>div{
    display: inline-block;
    vertical-align: top;
  }
  ul{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  li{
    display: inline-block;
    text-transform: uppercase;
    margin: 0 20px;
  }
  &__year{
    float: left;
  }
  &__socials{
    float: right;
  }
  &__social-icon{
    margin-left: 10px;
  }
}