.block{
  background: rgba(255,255,255,0.1);
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  padding: 32px 20px 28px;
  text-align: center;
  margin: auto;
  width: 100%;
  max-width: 460px;
  .btn{
    margin-bottom: 28px;
  }
  &--white{
    background-color: @white;
    box-shadow: 0px 6px 8px rgba(4, 17, 68, 0.1);
    border-radius: 4px;
  }
  &--small{
    padding: 10px 20px 18px;
  }
  &--fullwidth{
    max-width: none;
  }
  &--popup{
    position: relative;
    .block__close{
      position: absolute;
      width: 10px;
      height: 9px;
      right: 7px;
      top: 7px;
      img{
        vertical-align: top;
      }
    }
  }
  &--subscribe{
    width: 260px;
    padding: 18px 25px;
    .field{
      margin: 10px 0;
    }
    .btn{
      margin: 0;
    }
    .block__back{
      float: left;
    }
    .block__confirm{
      float: right;
    }
  }
  &--bordered{
    border: 2px solid #F4F4F4;
    box-sizing: border-box;
    border-radius: 4px;
    background-color: transparent;
    box-shadow: none;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 50px;
  }
}

.plus-block{
  width: 33%;
  display: inline-block;
  vertical-align: top;
  float: left;
  text-align: center;
  padding: 0 10px;
  margin-top: 40px;
  &__title{
    &:extend(h3);
    color: @blue;
    font-weight: 700;
    margin-bottom: 25px;
  }
}