.account-banner{
  height: 60px*4;
  background: linear-gradient(97.01deg, #041144 3.07%, #339BE6 114.17%);
  display: flex;
  justify-content: center;
  position: relative;
  flex-direction: column;
  &__content{
    font-size: 30em/16;
  }
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 160px;
    background-image: url(../img/svg/waves.svg);
    background-size: cover;
    background-repeat: no-repeat;
  }
}
